.project-item {
  background-color: #f5f5dc; /* Beige background color */
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: calc(50% - 40px); /* Adjust width to fit two items side by side */
  box-sizing: border-box;
}

.project-item h3 {
  margin-top: 0;
}

.project-item a {
  color: #000000; /* Black color for links */
  text-decoration: underline; /* Underline text from the start */
}

.project-item a:hover {
  text-decoration: underline dotted; /* Dotted underline on hover */
}

@media (max-width: 768px) {
  .project-item {
    width: calc(100% - 40px); /* Full width on small screens */
  }
}

